.profile-details {
  display: flex;
  margin: 2.5rem 0;
}

.photo-upload {
  flex-basis: 30%;
  margin: 0 1rem;
}

.photo-upload .fa-image {
  border: 1px solid var(--light-gray);
  border-radius: 5px;
  font-size: 48px;
  color: var(--light-gray);
  align-self: flex-start;
  padding: 1.5rem;
}

.photo-upload div {
  color: var(--bright-green);
  display: flex;
  justify-content: center;
  margin-top: 0.5rem;
  width: 100%;
}

.Dashboard .fa-plus {
  margin-right: 0.25rem;
}

.user-details {
  flex-basis: 70%;
  margin-right: 1rem;
}

.user-details p {
  border: 1px solid var(--light-gray);
  color: var(--text-gray);
  border-radius: 5px;
  margin-bottom: 1rem;
  padding: 1rem;
}

.user-details span {
  color: var(--bright-green);
  margin-right: 0.15rem;
}

.edit-button {
  color: var(--text-gray);
  display: flex;
  justify-content: flex-end;
  font-weight: lighter;
}

.user-details .edit-button p {
  border: none;
  padding: 0;
}
